import React from "react";
import "./App.css";
import Titles from "./Components/Titles";
import SelectButton from "./Components/SelectButton";
import DisplayWeatherResults from "./Components/DisplayWeatherResults";
import WeatherDecision from "./Components/WeatherDecision";

class App extends React.Component {
  state = {
    weatherData: [],
    city: undefined,
    temperature: undefined,
    imageID: undefined,
    loading: true,
    isHidden: true
  };

  getTheWeather = async e => {
    e.preventDefault();
    const url = `http://api.openweathermap.org/data/2.5/weather?id=5128638&units=imperial&appid=78c8954cfc788e1248bcd83dc32befc8`;
    const response = await fetch(url);
    const data = await response.json();
    this.setState({
      weatherData: data,
      city: data.name,
      temperature: data.main.temp,
      imageID: data.weather[0].icon,
      description: data.weather[0].description,
      loading: false,
      isHidden: false
    });
  };

  render() {
    return (
      <div className="container">
        <div className="titles-container">
          <Titles />
        </div>
        <div className="button-container">
          <SelectButton getTheWeather={this.getTheWeather} />
        </div>
        <div className="dialogue-container">
          {!this.state
            .isHidden /*Hide this component until data is fetched from API */ && (
            <div>
              <WeatherDecision temperature={this.state.temperature} />
              <DisplayWeatherResults
                city={this.state.city}
                temperature={this.state.temperature}
                imageID={this.state.imageID}
                description={this.state.description}
              />
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default App;
