import React from "react";
import Happy from "./Happy";
import Unhappy from "./Unhappy";

export default class WeatherDecision extends React.Component {
  decide(e) {
    if (this.props.temperature > 60) {
      return <Happy/>;
    } else {
      return <Unhappy/>;
    }
  }
  render() {
    return <div>{this.decide()}</div>;
  }
}
